import React from 'react';
import { connect } from 'react-redux';
import { EmailButton, KaKaoTalkButton, } from 'react-social-kr';
import EditButton from './edit-button';

const KAKAOTALK_KEY = 'aa837ddd8bedb198c9d7fddfd2aef8c0';


class SharePanel extends React.Component {

    resolveMessage({ team, teamA, teamB }) {
        let teamADetail = "";
        let teamBDetail = "";
        for (let key in teamA) {
            teamADetail += `${teamA[key].name}-득점:${teamA[key].point} 파울:${teamA[key].foul}`;
            teamADetail += " ";
        }
        for (let key in teamB) {
            teamBDetail += `${teamB[key].name}-득점:${teamB[key].point} 파울:${teamB[key].foul}`;
            teamBDetail += " ";
        }

        let message = `경기: ${team['teamA'].name} vs ${team['teamB'].name}`
            + `|결과`
            + `|득점: ${team['teamA'].point}:${team['teamB'].point}`
            + `|${team['teamA'].name}`
            + `|${teamADetail}`
            + `|${team['teamB'].name}`
            + `|${teamBDetail}`;

        return message;
    }

    render() {
        const { game } = this.props;
        const message = game.isPause ? this.resolveMessage(game) : "";

        return (
            <div >
                {
                    this.props.game.isPause ?
                        (
                            <div className="sharepanel">
                                <EmailButton message={'경기결과'} getLocation={() => message}>
                                    <div className="button email">Email</div>
                                </EmailButton>
                                <KaKaoTalkButton message={message} jsKey={KAKAOTALK_KEY} id={'kakaotalk'}>
                                    <div id="kakaotalk" className="button kakaotalk">KakaoTalk</div>
                                </KaKaoTalkButton>
                                <EditButton />
                            </div>
                        ) : null
                }

            </div>
        );
    }
}


const mapStateToProps = ({ game }) => {
    return { game };
};

export default connect(mapStateToProps, null)(SharePanel);