import React from 'react';
import PlayerPanel from './player-panel';

export function TeamPanelHeader(props) {
    return (
        <div className="header">
            <div className="info">
                <div className="name">name</div>
                <div className="point">point</div>
                <div className="foul">foul</div>
            </div>
            <div className="info">
                <div className="name">name</div>
                <div className="point">point</div>
                <div className="foul">foul</div>
            </div>
        </div>
    );
}

function TeamPanel(props) {
    return (
        <div className="teampanel">
            <PlayerPanel team={props.team} player={"player1"} />
            <PlayerPanel team={props.team} player={"player2"} />
            <PlayerPanel team={props.team} player={"player3"} />
            <PlayerPanel team={props.team} player={"player4"} />
        </div>
    );
}

export default TeamPanel;