import React from 'react';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import { togglePause } from '../store/game';

const GAME_CLOCK = 1000 * 60 * 10;
const SHOT_CLOCK = 1000 * 12;


class Clock extends React.Component {
    static propTypes = {
        gameClock: PropTypes.number,
        shotClock: PropTypes.number,
        decreaseClockSecond: PropTypes.func,
        resetShotClock: PropTypes.func
    }

    state = {
        isGameClockPause: true,
        isShotClockPause: true,
        gameClock: GAME_CLOCK,
        shotClock: SHOT_CLOCK
    }

    intervalSec = 100

    handleGameClockClick() {
        if (this.state.isGameClockPause) {
            this.setState({ isGameClockPause: false, isShotClockPause: false }, () => {
                this.interval = setInterval(() => { this.decreaseClockSecond(this.intervalSec) }, this.intervalSec);
            })
        } else {
            this.setState({ isGameClockPause: true, isShotClockPause: true }, () => {
                clearInterval(this.interval);
            });
        }

        this.props.togglePause();
    }

    handleShotClockClick() {
        const { isGameClockPause, isShotClockPause } = this.state;
        if (isGameClockPause && isShotClockPause) {
            this.setState({ shotClock: SHOT_CLOCK });
        } else if (isShotClockPause) {
            this.setState({ isShotClockPause: false });
        } else {
            this.setState({ isShotClockPause: true, shotClock: SHOT_CLOCK });
        }
    }

    decreaseClockSecond(milsec = 100) {
        this.setState(({ gameClock, shotClock }) => {
            let newState = { gameClock: Math.max(0, gameClock - milsec) };
            if (!this.state.isShotClockPause) {
                newState.shotClock = Math.max(0, shotClock - milsec);
            }
            return newState;
        });
    }

    resolveGameClock(gameClock) {
        const min = Math.floor(gameClock / (1000 * 60));
        const sec = (Math.floor((gameClock % (1000 * 60)) / 1000) + '').padStart(2, '0');
        return `${min}:${sec}`;
    }

    resolveShotClock(shotClock) {
        return `${shotClock / 1000}`;
    }

    render() {
        const { gameClock, shotClock } = this.state;

        return (
            <div className={"clock"}>
                <button onClick={() => { this.handleGameClockClick() }}>{this.resolveGameClock(gameClock)}</button>
                <button onClick={() => { this.handleShotClockClick() }}>{this.resolveShotClock(shotClock)}</button>
            </div>
        );

    }
}

const mapDispatchToProps = (dispatch) => ({
    togglePause: () => dispatch(togglePause())
})


export default connect(null, mapDispatchToProps)(Clock);