import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { inputTeamName, inputPlayerName } from '../store/game';

class NamePanel extends React.Component {
    static propTypes = {
        team: PropTypes.string,
        player: PropTypes.string
    }
    handleChange(e) {
        this.props.inputName(e.target.value);
    }

    render() {
        const { isEditMode, name, className } = this.props;
        return (
            <div className={className}>
                {isEditMode ? <input type="text" value={name} onChange={(e) => this.handleChange(e)} style={{width:'80%'}} /> : name}
            </div>
        )
    }
}

const mapStateToProps = ({ game }, { team, player }) => {
    if (team && player) {
        return { isEditMode: game.isEditMode, name: game[team][player].name };
    } else {
        return { isEditMode: game.isEditMode, name: game['team'][team].name };
    }

};

const mapDispatchToProps = (dispatch, { team, player }) => ({
    inputName: (name) => {
        if (team && player) {
            dispatch(inputPlayerName(team, player, name));
        } else {
            dispatch(inputTeamName(team, name));
        }
    }
})

export default connect(mapStateToProps, mapDispatchToProps)(NamePanel);