import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import NamePanel from './name-panel';
import { increasePlayerPoint, decreasePlayerPoint, increasePlayerFoul } from '../store/game';

class PlayerPanel extends React.Component {
    static propTypes = {
        increasePlayerPoint: PropTypes.func,
        decreasePlayerPoint: PropTypes.func
    }

    render() {
        const { playerInfo: { point, foul }, team, player } = this.props;
        return (
            <div className="playerpanel">
                <div className="info">
                    <NamePanel className="name" team={team} player={player}/>
                    <div className="point">{point}</div>
                    <div className="foul">{foul}</div>
                </div>
                <div className="button">
                    <button className="plus" onClick={() => { this.props.increasePlayerPoint() }}>+</button>
                    <button className="minus" onClick={() => { this.props.decreasePlayerPoint() }}>-</button>
                    <button className="foul" onClick={() => { this.props.increasePlayerFoul() }}>Foul</button>
                </div>
            </div>
        );

    }
}

const mapStateToProps = ({ game }, ownProps) => {
    return { playerInfo: game[ownProps.team][ownProps.player] };
};

const mapDispatchToProps = (dispatch, { team, player }) => ({
    increasePlayerPoint: () => dispatch(increasePlayerPoint(team, player)),
    decreasePlayerPoint: () => dispatch(decreasePlayerPoint(team, player)),
    increasePlayerFoul: () => dispatch(increasePlayerFoul(team, player)),
})

export default connect(mapStateToProps, mapDispatchToProps)(PlayerPanel);