import update from 'immutability-helper';

const INCREASE_PLAYER_POINT = "INCREASE_PLAYER_POINT";
const DECREASE_PLAYER_POINT = "DECREASE_PLAYER_POINT";
const INCREASE_PLAYER_FOUL = "INCREASE_PLAYER_FOUL";
const DECREASE_PLAYER_FOUL = "DECREASE_PLAYER_FOUL";
const INPUT_TEAM_NAME = "INPUT_TEAM_NAME";
const INPUT_PLAYER_NAME = "INPUT_PLAYER_NAME";
const SET_EDIT_MODE = "SET_EDIT_MODE";
const TOGGLE_PAUSE = "TOGGLE_PAUSE";

export const increasePlayerPoint = (team, player) => ({
    type: INCREASE_PLAYER_POINT,
    data: { team, player, attr: "point" }
});

export const decreasePlayerPoint = (team, player) => ({
    type: DECREASE_PLAYER_POINT,
    data: { team, player, attr: "point" }
});

export const increasePlayerFoul = (team, player) => ({
    type: INCREASE_PLAYER_FOUL,
    data: { team, player, attr: "foul" }
});

export const decreasePlayerFoul = (team, player) => ({
    type: DECREASE_PLAYER_FOUL,
    data: { team, player, attr: "foul" }
});

export const inputTeamName = (team, name) => ({
    type: INPUT_TEAM_NAME,
    data: { team, name }
});


export const inputPlayerName = (team, player, name) => ({
    type: INPUT_PLAYER_NAME,
    data: { team, player, name }
});

export const setEditMode = (value) => ({
    type: SET_EDIT_MODE,
    data: value
})

export const togglePause = () => ({
    type: TOGGLE_PAUSE
});

export const initialState = {
    isPause: true,
    isEditMode: false,
    team: {
        teamA: {
            name: 'teamA',
            point: 0,
            foul: 0
        },
        teamB: {
            name: 'teamB',
            point: 0,
            foul: 0
        }
    },
    teamA: {
        player1: {
            name: 'player1',
            point: 0,
            foul: 0
        },
        player2: {
            name: 'player2',
            point: 0,
            foul: 0
        },
        player3: {
            name: 'player3',
            point: 0,
            foul: 0
        },
        player4: {
            name: 'player4',
            point: 0,
            foul: 0
        }
    },
    teamB: {
        player1: {
            name: 'player1',
            point: 0,
            foul: 0
        },
        player2: {
            name: 'player2',
            point: 0,
            foul: 0
        },
        player3: {
            name: 'player3',
            point: 0,
            foul: 0
        },
        player4: {
            name: 'player4',
            point: 0,
            foul: 0
        }
    }
}

export default (state = initialState, action) => {
    switch (action.type) {
        case INCREASE_PLAYER_POINT:
        case INCREASE_PLAYER_FOUL:
            {
                const tk = action.data.team;
                const pk = action.data.player;
                const ak = action.data.attr;
                return update(update(state, { [tk]: { [pk]: { [ak]: { $set: state[tk][pk][ak] + 1 } } } }),
                    { team: { [tk]: { [ak]: { $set: state['team'][tk][ak] + 1 } } } });
            }
        case DECREASE_PLAYER_POINT:
        case DECREASE_PLAYER_FOUL:
            {
                const tk = action.data.team;
                const pk = action.data.player;
                const ak = action.data.attr;
                return update(update(state, { [tk]: { [pk]: { [ak]: { $set: Math.max(0, state[tk][pk][ak] - 1) } } } }),
                    { team: { [tk]: { [ak]: { $set: Math.max(0, state['team'][tk][ak] - 1) } } } });
            }
        case INPUT_TEAM_NAME:
            {
                const tk = action.data.team;
                const name = action.data.name;
                return update(state, { team: { [tk]: { $merge: { name } } } });
            }
        case INPUT_PLAYER_NAME:
            {
                const tk = action.data.team;
                const pk = action.data.player
                const name = action.data.name;
                return update(state, { [tk]: { [pk]: { $merge: { name } } } });
            }
        case SET_EDIT_MODE:
            return update(state, {isEditMode: {$set: action.data}});
        case TOGGLE_PAUSE:
            return update(state, {isPause: {$set: !state.isPause}});
        default:
            return state
    }
}